$primary: #3579ed;
$secondary: #ffab00;
$black10: rgba(0, 0, 0, 0.1);

.color-primary {
	color: $primary;
}

.color-secondary {
	color: $secondary;
}

body {
	margin: 0;
	padding: 0;
	font-family: 'Open Sans', 'Roboto', sans-serif;
	color: #3a3f52;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	@media (min-width: 992px) {
		padding-top: 83px;
	}

	@media (max-width: 991px) {
		padding-top: 59px;
	}
}

strong {
	font-weight: 600;
}

h1 {
	font-size: 1.5rem;

	@media (min-width: 992px) {
		font-size: 2.5rem;
	}
}

h2 {
	font-size: 1.3rem;

	@media (min-width: 992px) {
		font-size: 2rem;
	}
}

a {
	&.text-link {
		color: rgba(0, 0, 0, 0.83);
		text-decoration: underline;
		margin: 0 2px 0 4px;
	}

	&:hover {
		text-decoration: none;
	}
}

p,
li {
	font-weight: 300;
	color: rgba(0, 0, 0, 0.83);

	@media(min-width: 992px) {
		letter-spacing: .05em;
	}
}

ul {
	@media(max-width: 991px) {
		padding-left: 20px;
	}

	li {
		font-size: 15px;
		line-height: 20px;
		margin-bottom: 10px;
	}
}

hr {
	margin: 32px 0;
}

section {
	@media (min-width: 992px) {
		margin: 100px 0px;
	}

	@media (max-width: 991px) {
		margin: 30px 0;
	}
}

.crop {
	position: relative;
	width: 100%;
	padding-top: 100%;
	overflow: hidden;
}

.crop img {
	position: absolute;
	left: 50%;
	top: 50%;
	height: 100%;
	width: auto;
	-webkit-transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}

.crop img.portrait {
	width: 100%;
	height: auto;
}

/* ==============================================
     2. Navbar 
  ===============================================*/

.navbar.navbar-light {
	background-color: white; //box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.09);

	.navbar-brand {
		padding-left: 0;
	}

	.logo-img {
		max-height: 48px;
		border: 3px solid white;
		border-radius: 50%;
		margin: -4px 16px 0 0;
		box-shadow: 0 1px 1px rgba(0, 0, 0, .07);
	}

	.logo-text {
		display: inline-block;
		letter-spacing: 1px;
		margin: 0;
		font-size: 1.3rem;
		font-weight: 700;
	}

	.nav-link {
		font-size: 15px;
		font-weight: 400;
		letter-spacing: .05em;
		padding: 10px 16px;
		display: block;

		&:hover {
			text-decoration: none;
		}

		&.active {
			font-weight: 500;
		}
	}

	.navbar-toggler {
		padding: .25rem;
		border: none;
	}

	/* Large devices (desktops, 992px and up) */
	@media (min-width: 992px) {
		padding: 20px;

		&.navbar-expand-lg .navbar-nav .nav-link {
			padding-left: 40px;
		}
	}

	@media (max-width: 991px) {
		
		.logo-img {
			position: absolute;
			left: 50%;
			-webkit-transform: translateX(-50%);
			-ms-transform: translateX(-50%);
			transform: translateX(-50%);
		}

		.logo-text {
			font-size: 1.1rem;
			letter-spacing: 0;
		}

		.navbar-collapse::after {
			content: "";
			position: fixed;
			top: 256px;
			right: 0;
			bottom: 0;
			left: 0;
			background: rgba(0, 0, 0, 0.4);
		}
	}
}

