/* ==============================================
     5. Home
  ===============================================*/

#home {
	.hero {
		position: relative;
		padding: 27vh 0;
		margin-top: 0;

		h2 {
			font-size: 50px;
			font-weight: 300;
		}

		h3 {
			font-weight: 600;
			font-size: 40px;
			margin-bottom: 40px;
		}

		p {
			font-size: 20px;
			font-weight: 300;
			padding: 0px;
		}

		.flipper {
			display: block;
			margin: 0 auto;
			perspective: 1000;
			@include clearfix;
			&:hover .flipper-back {
				transform: rotateY(0deg);
			}

			&:hover .flipper-front {
				transform: rotateY(-180deg);
			}

			.flipper-content {
				transform: perspective(1000px);
				transform-style: preserve-3d;
				position: relative;
			}

			.flipper-front,
			.flipper-back {
				position: absolute;
				top: 0;
				left: 0;
				text-align: center;
				backface-visibility: hidden;
				transition: 0.6s;
				transform-style: preserve-3d;
			}

			.flipper-front {
				z-index: 2;
			}

			.flipper-back {
				transform: rotateY(180deg);
			}
		}

		.avatar {
			width: 100%;
		}

		@media (min-width: 992px) {
			margin-bottom: 60px;

		}

		@media (max-width: 991px) {
			padding: 15vh 0;
			text-align: center;
			h2 {
				font-size: 24px;
			}

			h3 {
				font-size: 24px;
			}
			.flipper{
				min-height: 205px;
			}
			.avatar {
				width: 50%;
			}
			p{
				font-size: 14px;
			}
			
		}
	}

	.particles-layout {
		background: #0052e9;
	}
}

/* ==============================================
     6. Portfolio
  ===============================================*/

#portfolio {
	h3 {
		font-size: 1.4em;
		font-weight: 500;

		&:not(:first-child) {
			margin-top: 50px;
		}
	}

	h4 {
		font-size: 1.1em;
		margin-bottom: 20px;

		&:not(:first-child) {
			margin-top: 50px;
		}
	}

	h5 {
		margin-top: 30px;
	}

	.hero {
		padding: 0;
		background-repeat: no-repeat;
		background-position: center center;
		background-size: cover; // background-attachment: fixed;
		height: 50vh;
	}

	.title-block {
		margin-bottom: 50px;
		padding-bottom: 50px;

		@media(max-widh: 991px) {
			margin-bottom: 10px;
		}
	}

	.img-fluid {
		margin: 30px 0;
	}

	.card {
		position: relative;
		opacity: 0.8;
		transition: 0.3s all;
		border: none;
		margin-bottom: 40px;

		&:hover {
			transition: 0.3s all;
			opacity: 1;
			box-shadow: 0px 22px 43px rgba(68, 68, 68, 0.2);
			margin-top: -5px;

			.card-body {
				transition: 0.3s all;
				padding-left: 1.25rem;
			}
		}

		&.coming {
			transition: none;

			&:hover {
				transition: none;
				margin-top: 0;
				box-shadow: none;
				opacity: 0.8;

				.card-body {
					padding-left: 0;
				}
			}

			img {
				-webkit-filter: grayscale(100%);
				filter: grayscale(100%);
			}

			.card-title {
				opacity: 0.2;
			}
		}

		.card-body {
			transition: 0.3s all;
			position: relative;
			padding-left: 0;
		}

		.card-title {
			color: rgba(0, 0, 0, 0.87);
			font-weight: 700;
			display: inline-block;
		}

		.card-title:last-child {
			margin-bottom: 0;
		}

		@media (min-width: 992px) {
			.card-title {
				font-size: 28px;
				letter-spacing: -0.5px;
			}
		}

		@media (max-width: 991px) {}
	}
}

